.page {
  @include minorFontStyle;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 100%;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all $effectDuration;
    opacity: 0;
    transform: translateX(-100%);

    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
